.search-block {
  background-color: @gray;
}

.search-block__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 32px;
  padding-bottom: 25px;

  @media screen and (max-width: @576) {
    flex-direction: column;
  }
}

.search-block__txt {

}

.search-block__breadcrumbs {
  margin-bottom: 4px;
}

.search-block__title {
  font-size: 46px;
  line-height: 66px;
  font-family: @jost;
  color: @black;
  font-weight: 400;
  margin: 0;

  @media screen and (max-width: @576) {
    font-size: 36px;
    line-height: 52px;
  }
}

.search-block__search-form {
  width: 100%;
  height: 32px;
  max-width: 388px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid @black;
}

.search-block__input {
  flex-grow: 1;
  font-size: 16px;
  line-height: 19px;
  color: @black;
  outline: none;
  border: none;
  background-color: transparent;

  &::placeholder {
    font: inherit;
  }
}

.search-block__submit {
  width: 20px;
  height: 20px;
  padding: 0;
  outline: none;
  border: none;
  background-color: transparent;
  transition: all ease-in 0.2s;
  cursor: pointer;

  &:hover {
    opacity: 0.8;

    svg {
      width: 16px;
      height: 16px;

      fill:@orange;
    }
  }

  &:active,
  &:focus {
    opacity: 0.6;
  }

  svg {
    width: 16px;
    height: 16px;
    transition: all ease-in 0.2s;

    fill: @black;
  }
}
