.catalog {
  padding-bottom: 60px;
}

.catalog__main-wrapper {
  padding-top: 82px;
  display: flex;
  justify-content: space-between;

  @media screen and (max-width: @576) {
    padding-top: 42px;
  }
}

.ui-slider-horizontal .ui-slider-range {
  height: 2px;
  background-color: @black;
}

.ui-slider-horizontal .ui-slider-handle {
  background-color: @black;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  outline: none;
  border: none;
  top: -0.35em;
}

.ui-widget.ui-widget-content {
  height: 1px;
  background-color: @gray;
  border: none;
}

.catalog__filters {
  width: 100%;
  max-width: 380px;

  @media screen and (max-width: @1280) {
    max-width: 344px;
  }

  @media screen and (max-width: @1024) {
    display: none;
    position: fixed;
    padding: 50px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    background-color: @white;
    max-width: 80%;
  }

  @media screen and (max-width: @576) {
    max-width: 100%;
    padding: 15px;
    padding-top: 40px;
    height: 100vh;
    overflow-y: scroll;
  }
}

.catalog__filters-btn {
  display: none;

  @media screen and (max-width: @1024) {
    display: block;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: @black;
    padding: 15px 21px;
    border: 1px solid @gray;
    background-color: transparent;
    letter-spacing: 0.08em;
    margin-bottom: 30px;

    svg {
      margin-bottom: -3px;
      margin-left: 15px;

      fill: @black;
    }
  }

  @media screen and (max-width: @576) {
    width: 100%;
  }
}

.catalog__filters-btn-close {
  display: none;
  width: 50px;
  height: 50px;
  background: transparent;
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;

  svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }

  @media screen and (max-width: @1024) {
    display: block;
  }

  @media screen and (max-width: @576) {
    right: 15px;
  }
}

.catalog__filter-title {
  display: block;
  font-size: 20px;
  line-height: 28px;
  color: @black;
  font-weight: 500;
  font-family: @jost;
  margin-bottom: 22px;
}

.catalog__slider {
  width: calc(100% - 18px);
  margin: 0 auto;
  margin-bottom: 26px;
}

.catalog__form-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;

  @media screen and (max-width: @576) {
    flex-wrap: wrap;
    flex-direction: column-reverse;

    margin-bottom: 30px;
  }
}

.catalog__form-submit {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: @black;
  padding: 15px 21px;
  border: 1px solid @gray;
  background-color: transparent;
  letter-spacing: 0.08em;
}

.catalog__form-price {
  display: flex;
  align-items: center;

  @media screen and (max-width: @576) {
    margin-bottom: 20px;
  }

  .catalog__form-price,
  .catalog__form-txt {
    flex-grow: 1;

    @media screen and (max-width: @576) {
      flex-grow: 0;
    }
  }
}

.catalog__form-input {
  font-size: 16px;
  font-family: @roboto;
  line-height: 19px;
  color: @black;
  font-weight: 400;
  width: 100%;
  padding: 0 8px;
  text-align: center;
  border: none;
  outline: none;
}

.catalog__form-input-buffer {
  position: absolute;
  top: -1000px;
  left: -1000px;
  visibility: hidden;
  white-space: nowrap;
}

.catalog__params-name {
  font-family: @jost;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  display: block;
  margin-bottom: 22px;
}

.catalog__params-list {
  display: flex;
  flex-wrap: wrap;
}

.catalog__params-item {
  margin-right: 12px;
  margin-bottom: 12px;
}

.catalog__params-label {
  border: 1px solid @gray;
  padding: 7px 15px;
  font-size: 12px;
  line-height: 14px;
  display: block;

  &:focus {
    background-image: @gradient;
  }
}

.catalog__params-checkbox {
  display: none;

  &:checked + .catalog__params-label {
    background-image: @gradient;
  }
}

.catalog__sort-row {
  margin-bottom: 50px;

  @media screen and (max-width: @576) {
    margin-bottom: 20px;
  }
}

.catalog__content {
  flex-grow: 1;
  margin-left: 52px;
  width: 100%;
  max-width: 844px;

  @media screen and (max-width: @1280) {
    max-width: 844px;
    margin-left: auto;
  }

  @media screen and (max-width: @1024) {
    max-width: 100%;
    margin-left: 0;
  }
}

.catalog__remove-filters-wrapper {
  display: flex;
  flex-wrap: wrap;

  @media screen and (max-width: @576) {
    margin-bottom: 25px;
    justify-content: space-between;
  }
}

.catalog__popular {
  @media screen and (max-width: @576) {
    width: 100%;
  }
}

.catalog__remove-filters-item {
  border: 1px solid @gray;
  padding: 7px 15px;
  font-size: 12px;
  line-height: 14px;
  display: block;
  margin-right: 12px;
  margin-bottom: 12px;
  background-color: transparent;

  @media screen and (max-width: @576) {
    width: 48%;
    text-align: center;
    margin-right: 0;
  }
}

.catalog__remove-filters-params-name {
  &::after {
    content: ":";
  }
}

.catalog__remove-filters-close {
  padding: 0;
  border: 0;
  background-color: transparent;
}

.catalog__content-top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;

  @media screen and (max-width: @576) {
    flex-direction: column;
  }
}

.catalog__list {
  display: flex;
  flex-wrap: wrap;
}

.catalog__item {
  width: 100%;
  max-width: 406px;
  position: relative;
  margin-bottom: 40px;

  @media screen and (max-width: @1280) {
    max-width: 388px;
    margin-right: auto;
    margin-left: auto;
  }

  &:nth-child(2n -1) {
    margin-right: auto;

    @media screen and (max-width: @1280) {
      margin-right: auto;
      margin-left: auto;
    }
  }

  @media screen and (max-width: @1024) {
    max-width: 328px;
    margin-left: 0;
  }
}

.catalog__img {
  display: block;
  margin-bottom: 20px;

  @media screen and (max-width: @1280) {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
}

.catalog__item-txt {
  text-align: center;
}

.catalog__item-title {
  font-size: 20px;
  line-height: 28px;
  font-weight: 500;
  font-family: @jost;
  color: @black;
  margin-bottom: 4px;
}

.catalog__item-title-link {
  font: inherit;
  color: inherit;
}

.catalog__size {
  font-size: 16px;
  line-height: 19px;
  color: @black;
  margin-bottom: 8px;
}

.catalog__price {
  font-size: 28px;
  line-height: 32px;
  font-weight: 500;
  text-transform: uppercase;
}

.catalog__item-cart-btn {
  padding: 0;
  border: none;
  background-color: transparent;
  position: absolute;
  top: 36px;
  left: 36px;
}

.catalog__item-comparison-btn {
  padding: 0;
  border: none;
  background-color: transparent;
  position: absolute;
  top: 36px;
  right: 30px;
}
