* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #34393B;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
p {
  margin: 0;
}
.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
a {
  text-decoration: none;
  transition: all ease-in 0.3s;
  outline: none;
}
button {
  outline: none;
}
.container {
